.chat-full-page{
    display: grid;
    place-items: center;
    direction: rtl;
}

.chat{
    flex: 0.65;    
    display: flex;
    flex-direction: column;
}

.chat-room-name{
    margin: 0px;
}

.chat-room-last-seen{
    margin: 0;
}

.chat_header{
    display: flex;
    padding: 20px;
    align-items: center;
    border-bottom: 1px solid lightgray;
}

.chat_headerInfo{
    padding-left: 20px;
    padding-right: 10px;
    flex: 1;
    text-align: right;
}

.chat_headerInfo > h3 {
    margin: 3px;
    font-weight: 500;
}

.app_body{
    display: flex;
    background-color: #ededed;
    height: 90vh;
    width: 90vw;
    margin-top: 50px;
    box-shadow: -1px 4px 20px -6px rgba(0, 0, 0, 0.2);
    max-width: 1100px;
    border-radius: 30px !important;
}

.chat_headerInfo > p {
    color:gray;
}

.chat_headerRight{
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}

.chat_body{
    flex: 1;
    background-image: url('https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png');
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow-y: scroll;
    direction: ltr;
}

.chat_message{
    position: relative;
    font-size: 16px;
    border-radius: 10px;
    padding: 10px;
    background-color: white;
    width: fit-content;
    margin-top: 30px;
}

.chat_name{
    font-weight: 700;
    font-size: small;
    display: inline-block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.hide {
    display: none;
}

.chat_timestemp{
    margin-top: 5px;
    font-size: xx-small;
    display: block;
}

.chat_receiver {
    margin-left: auto;
    background-color: #dcf8c6!important;
    color: black !important;
}

.chat_footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 62px;
    border: 1px solid lightgray;
    border-bottom-left-radius: 30px;
}

.chat_footer > form{
    flex: 1;
    display: flex;
}

.chat_footer > form > input{
    flex: 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
    margin: 0 10px 0px 10px;
}

.chat_footer > form > button{
    display: none;
}

.app{
    display: grid;
    place-items: center;
    background-color: #1e293b;
    height: 100vh;
}